<template>
  <div>
    <el-row class="breadcrumb-style">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">防护管理</el-breadcrumb-item>
        <el-breadcrumb-item>网站防护</el-breadcrumb-item>
      </el-breadcrumb>
    </el-row>
    <el-row class="container-style">
      <el-col :span="24" v-loading.fullscreen.lock="loadingPage">
        <el-row class="text-align-right">
          <div class="data-search-input">
            <el-input placeholder="请输入关键词进行搜索" prefix-icon="el-icon-search" v-model="dataSearch"
              @input="onChangeSearch">
            </el-input>
            <el-button icon="el-icon-search" @click="onChangeSearch" class="search-icon-btn" />
          </div>
          <el-button type="primary" @click="onClickCreateDomain()">新增网站</el-button>
        </el-row>
        <div class="demo-block">
          <el-table :data="tableData" style="width: 100%">
            <el-table-column prop="domain" label="网站"></el-table-column>
            <el-table-column label="网站配置">
              <template #default="scope">
                <p>协议类型：
                  <el-tag v-if="scope.row.http == 'true'" size="small">HTTP</el-tag>
                  <el-tag v-if="scope.row.https == 'true'" size="small">HTTPS</el-tag>
                </p>
                <p class="col-item-box">源站地址：
                  <span class="col-item-content">
                    <span v-for="( item, index ) in scope.row.source_ip.split(',')" :key="index">
                      <el-tag size="small">{{ item }}</el-tag>
                    </span>
                  </span>
                </p>
                <p>源站端口：
                  <el-tag size="small">{{ scope.row.source_http_port }}</el-tag>
                </p>

                <p>回源协议：
                  <el-tag size="small" v-if="scope.row.proxy_pass_https == 'false'">HTTP</el-tag>
                  <el-tag size="small" v-if="scope.row.proxy_pass_https == 'true'">HTTPS</el-tag>
                  
                </p>
                <p>负载均衡：
                  <el-tag size="small" v-if="scope.row.balance_type == 'round_robin'">轮询</el-tag>
                  <el-tag size="small" v-if="scope.row.balance_type == 'ip_hash'">IP_HASH</el-tag>
                </p>
              </template>
            </el-table-column>

            <el-table-column prop="protection" label="防护设置">
              <template #default="scope">
                <p>
                  <span class="col-item-protection-title">Web防护引擎：</span>
                  <span v-if="scope.row.web_engine_protection == 'true'">
                    <el-tag size="small" type="success">已开启</el-tag>
                  </span>
                  <span v-if="scope.row.web_engine_protection == 'false'">
                    <el-tag size="small" type="warning">未开启</el-tag>
                  </span>
                </p>
                <p>
                  <span class="col-item-protection-title">Web规则防护：</span>
                  <span v-if="scope.row.web_rule_protection == 'true'">
                    <el-tag size="small" type="success">已开启</el-tag>
                  </span>
                  <span v-if="scope.row.web_rule_protection == 'false'">
                    <el-tag size="small" type="warning">未开启</el-tag>
                  </span>
                </p>
                <p>
                  <span class="col-item-protection-title">Web白名单规则：</span>
                  <span v-if="scope.row.web_white_rule == 'true'">
                    <el-tag size="small" type="success">已开启</el-tag>
                  </span>
                  <span v-if="scope.row.web_white_rule == 'false'">
                    <el-tag size="small" type="warning">未开启</el-tag>
                  </span>
                </p>
                <p>
                  <span class="col-item-protection-title">流量防护引擎：</span>
                  <span v-if="scope.row.flow_engine_protection == 'true'">
                    <el-tag size="small" type="success">已开启</el-tag>
                  </span>
                  <span v-if="scope.row.flow_engine_protection == 'false'">
                    <el-tag size="small" type="warning">未开启</el-tag>
                  </span>
                </p>
                <p>
                  <span class="col-item-protection-title">流量规则防护：</span>
                  <span v-if="scope.row.flow_rule_protection == 'true'">
                    <el-tag size="small" type="success">已开启</el-tag>
                  </span>
                  <span v-if="scope.row.flow_rule_protection == 'false'">
                    <el-tag size="small" type="warning">未开启</el-tag>
                  </span>
                </p>
                <p>
                  <span class="col-item-protection-title">IP区域封禁：</span>
                  <span v-if="scope.row.flow_ip_region_block == 'true'">
                    <el-tag size="small" type="success">已开启</el-tag>
                  </span>
                  <span v-if="scope.row.flow_ip_region_block == 'false'">
                    <el-tag size="small" type="warning">未开启</el-tag>
                  </span>
                </p>
                <p>
                  <span class="col-item-protection-title">流量白名单规则：</span>
                  <span v-if="scope.row.flow_white_rule == 'true'">
                    <el-tag size="small" type="success">已开启</el-tag>
                  </span>
                  <span v-if="scope.row.flow_white_rule == 'false'">
                    <el-tag size="small" type="warning">未开启</el-tag>
                  </span>
                </p>
              </template>
            </el-table-column>
            <el-table-column label="操作" align="right">
              <template #default="scope">
                <el-button size="mini" @click="handleEdit(scope.row)" class="button-block" type="text"
                  :loading="scope.row.loading">网站配置</el-button>
                <el-button size="mini" @click="handleProtection(scope.row)" class="button-block" type="text">防护配置
                </el-button>
                <el-popover placement="top" width="160" v-model:visible="scope.row.isVisiblePopover">
                  <p>确定删除吗？</p>
                  <div style="text-align: right; margin: 0">
                    <el-button size="mini" type="text" @click="scope.row.isVisiblePopover = false">取消</el-button>
                    <el-button type="primary" size="mini" @click="handleDelete(scope.row)" :loading="loading">确定
                    </el-button>
                  </div>
                  <template #reference>
                    <el-button type="text" size="mini" @click="scope.row.isVisiblePopover = true">删除</el-button>
                  </template>

                </el-popover>
              </template>
            </el-table-column>
          </el-table>

          <el-pagination background layout="prev, pager, next" :total="tableTotal" v-model:current-page="currentPage"
            :page-size="50" @current-change="onCurrentChange" />
        </div>
      </el-col>

      <el-dialog :title="domainTitle" v-model="dialogDomainFormVisible" width="580px" :close-on-click-modal="false"
        @closed="dialogClose">
        <el-form class="form-tag-dialog" :model="domainForm" size="mini" label-position="left" label-width="190px"
          :rules="rules" ref="domainForm">
          <el-form-item label="域名/IP" prop="domain" key="1">
            <el-input v-model="domainForm.domain" v-if="domainType == 'new'"
              placeholder="请输入IP或域名，域名支持通配符，例如*.jxwaf.com">
            </el-input>
            <el-input v-model="domainForm.domain" v-if="domainType == 'edit'" disabled="disabled"></el-input>
            <p class="form-info-color" v-if="domainType == 'new'">
              （请输入IP或域名，域名支持通配符，例如*.jxwaf.com）
            </p>
          </el-form-item>
          <el-form-item label="协议类型" prop="checkListProtocol" key="2">
            <el-checkbox-group v-model="domainForm.checkListProtocol" size="small">
              <el-checkbox label="HTTP" key="HTTP"></el-checkbox>
              <el-checkbox label="HTTPS" key="HTTPS"></el-checkbox>
            </el-checkbox-group>
          </el-form-item>

          <div v-if="domainForm.checkListProtocol.indexOf('HTTPS') > -1">
            <el-tabs type="border-card" class="domain-tabs" v-model="selectTabsValue">
              <el-tab-pane label="SSL证书管理" name="0">
                <el-form-item label="SSL证书" key="3">
                  <el-select v-model="domainForm.ssl_domain" placeholder="请选择或输入模糊搜索" filterable>
                    <el-option v-for="item in sslOptions" :key="item.ssl_domain" :label="item.ssl_domain"
                      :value="item.ssl_domain">
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-tab-pane>

            </el-tabs>
          </div>
          <el-form-item key="6">
            <template #label>
              <span style="margin-right: 4px;color: #f56c6c;">*</span>源站地址
            </template>
            <el-tag :key="index" v-for="(tag, index) in sourceIpList" closable :disable-transitions="false"
              @close="handleCloseSourceIpList(tag)">{{ tag }}</el-tag>
            <el-input class="input-new-tag node-ip-list" v-if="sourceIpListVisible" v-model="sourceIpListValue"
              ref="saveTagSourceIpList" size="mini" @keyup.enter="handleSourceIpListConfirm"
              @blur="handleSourceIpListConfirm"></el-input>
            <el-button v-else class="button-new-tag" size="small" @click="showSourceIpList">
              <i class="el-icon-plus"></i>
            </el-button>
            <p class="form-info-color">
              （支持IP和域名，域名需要省略https:// 或 http://）
            </p>
          </el-form-item>

          <el-form-item label="源站端口" prop="source_http_port" key="7">
            <el-input placeholder="仅支持http" v-model="domainForm.source_http_port"></el-input>
          </el-form-item>

          <el-form-item label="回源协议" prop="proxy_pass_https" key="8">
            <el-radio v-model="domainForm.proxy_pass_https" label="false">http</el-radio>
            <el-radio v-model="domainForm.proxy_pass_https" label="true">https</el-radio>
            
          </el-form-item>
          <el-form-item label="负载均衡" prop="balance_type" key="14">
            <el-radio v-model="domainForm.balance_type" label="ip_hash">IP_HASH</el-radio>
            <el-radio v-model="domainForm.balance_type" label="round_robin">轮询</el-radio>
          </el-form-item>
          <el-form-item label="高级配置" key="9">
            <el-switch v-model="domainForm.advanced_conf" size="small" active-value="true" inactive-value="false" />
          </el-form-item>
          <div v-if="domainForm.advanced_conf == 'true'">
            <el-form-item label="强制HTTPS跳转" key="10" prop="force_https">
              <el-radio v-model="domainForm.force_https" label="true">是</el-radio>
              <el-radio v-model="domainForm.force_https" label="false">否</el-radio>
            </el-form-item>
            <el-form-item label="WAF前存在代理" key="11" prop="pre_proxy">
              <el-radio v-model="domainForm.pre_proxy" label="true">是</el-radio>
              <el-radio v-model="domainForm.pre_proxy" label="false">否</el-radio>
            </el-form-item>
            <div v-if="domainForm.pre_proxy == 'true'">
              <el-form-item label="HTTP请求头获取真实IP" key="12" prop="real_ip_conf">
                <el-radio v-model="domainForm.real_ip_conf" label="XRI">X-Real-IP</el-radio>
                <el-radio v-model="domainForm.real_ip_conf" label="XFF">X-Forwarded-For</el-radio>
              </el-form-item>
              
            </div>
          </div>

        </el-form>
        <template #footer class="dialog-footer">
          <el-button @click="dialogDomainFormVisible = false">取消</el-button>
          <el-button type="primary" @click="onClickDomainSubmit('domainForm')" :loading="loading">确定</el-button>
        </template>
      </el-dialog>
    </el-row>
  </div>

</template>
<script>
import { validatePort, validateDomainPort, mixin, JXAjax } from "../assets/scripts/common";
export default {
  mixins: [mixin],
  data() {
    return {
      domainTitle: "新增网站",
      domainType: "new",
      dataSearch: "",
      loadingPage:false,
      dialogDomainFormVisible: false,
      loading: false,
      domainForm: {
        source_http_port: "80",
        proxy_pass_https: "false",
        advanced_conf: "false",
        force_https: "false",
        pre_proxy: "false",
        real_ip_conf: "XRI",
        balance_type:"ip_hash",
        white_ip_list: "",
        checkListProtocol: [],
      },

      tableData: [],
      sourceIpList: [],
      sourceIpListVisible: false,
      sourceIpListValue: "",
      flagTag: true,
      sslOptions: [],
      selectTabsValue: "0",
      currentPage: 1,
      tableTotal: 0,
      path:"",
    };
  },
  computed: {
    rules() {
      return {
        domain: [
          {
            required: true,
            message: '请输入网站地址',
            trigger: ["blur", "change"],
          },
          {
            validator: validateDomainPort,
            trigger: ["blur", "change"],
          },
        ],
        source_http_port: [
          {
            required: true,
            message: '请输入源站端口信息',
            trigger: ["blur", "change"],
          },
          {
            validator: validatePort,
            trigger: ["blur", "change"],
          },
        ],
        checkListProtocol: [
          {
            type: "array",
            required: true,
            message: '请至少选择一项协议类型',
            trigger: "change",
          },
        ],
        proxy_pass_https: [
          { required: true, message: '请选择回源协议', trigger: 'change', },
        ],
        real_ip_conf: [
          { required: true, message: '请选择', trigger: 'change', },
        ],
        balance_type: [
          { required: true, message: '请选择', trigger: 'change', },
        ],
        pre_proxy: [
          { required: true, message: '请选择', trigger: 'change', },
        ],
        force_https: [
          { required: true, message: '请选择', trigger: 'change', },
        ],
       
      }
    }
  },
  
  mounted() {
    this.onCurrentChange(this.currentPage);
  },
  methods: {
    handleCloseSourceIpList(tag) {
      this.sourceIpList.splice(this.sourceIpList.indexOf(tag), 1);
    },
    showSourceIpList() {
      this.sourceIpListVisible = true;
      this.$nextTick((_) => {
        this.$refs.saveTagSourceIpList.$refs.input.focus();
      });
    },
    handleSourceIpListConfirm() {
      let t = this;
      let sourceIpListValue = this.sourceIpListValue;
      //let pattern = /^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])$/;
      //let pattern = /^(((https|http)?:\/)?\/)/;
      let pattern = /^(((https|http)?:\/)?\/)/;
      if (t.flagTag) {
        t.flagTag = false;
        if (sourceIpListValue) {
          if (pattern.test(sourceIpListValue)) {
            // JXAjax(
            //   "post",
            //   "/ip_check",
            //   { ip: sourceIpListValue },
            //   function (response) {
            //     if (response.data.result) {
            //       t.sourceIpList.push(sourceIpListValue);
            //       t.sourceIpListVisible = false;
            //       t.sourceIpListValue = "";
            //     } else {
            //       t.$message({
            //         showClose: true,
            //         message: "请输入正确的IP地址",
            //         type: "error",
            //       });
            //     }
            //   },
            //   function () {
                
            //   },
            //   "no-message"
            // );
            t.$message({
              showClose: true,
              message: "请输入正确的域名格式",
              type: "error",
            });
          } else {
            t.sourceIpList.push(sourceIpListValue);
            t.sourceIpListVisible = false;
            t.sourceIpListValue = "";
          }
        } else {
          t.sourceIpListVisible = false;
          t.sourceIpListValue = "";
        }
        setTimeout(function () {
          t.flagTag = true;
        }, 50);
      }
    },

    getData(page) {
      var t = this;
      JXAjax(
        "post",
        "/waf/waf_get_domain_list",
        { page : page},
        function (response) {
          t.tableData = response.data.message;
          t.tableData.forEach(item => {
            item.isVisiblePopover = false;
          })
          //t.loadingPage = false;
          t.tableTotal = response.data.count;
          t.currentPage = response.data.now_page;
        },
        function () {
          //t.loadingPage = false;
        },
        "no-message"
      );
    },

    dialogClose() {
      this.domainForm = {
        source_http_port: "80",
        proxy_pass_https: "false",
        advanced_conf: "false",
        force_https: "false",
        pre_proxy: "false",
        real_ip_conf: "XRI",
        balance_type: "ip_hash",
        white_ip_list: "",
        checkListProtocol: [],
      };
      this.selectTabsValue = "0";
      this.sourceIpListVisible = false;
      this.sourceIpListValue = "";
      this.sourceIpList = [];
      this.$refs["domainForm"].resetFields();
    },

    onClickDomainSubmit(domainForm) {
      var t = this;
      var arrProtocol = t.domainForm.checkListProtocol;
      var postUrl = "/waf/waf_create_domain";
      if (t.domainType == "edit") {
        postUrl = "/waf/waf_edit_domain";
      }
      if (t.sourceIpList.length == 0) {
        t.$message({
          message: '源站地址不能为空',
          type: "error",
        });
        return false;
      }
      
      if (t.domainForm.checkListProtocol.indexOf('HTTPS') < 0) {
        t.domainForm.ssl_source = "ssl_manage";
        t.domainForm.ssl_domain = "";
        
      } else {
        if (t.selectTabsValue == "0") {
          if (t.domainForm.ssl_domain == undefined || t.domainForm.ssl_domain == "") {
            t.$message({
              message: '请选择SSL证书',
              type: "error",
            });
            return false;
          } else {
            t.domainForm.ssl_source = "ssl_manage";
          }
        }
      }
      t.domainForm.source_ip = t.sourceIpList.join(",");
      t.domainForm.http = arrProtocol.indexOf("HTTP") > -1 ? "true" : "false";
      t.domainForm.https = arrProtocol.indexOf("HTTPS") > -1 ? "true" : "false";
      this.$refs[domainForm].validate((valid) => {
        if (valid) {
          t.loading = true;
          JXAjax(
            "post",
            postUrl,
            t.domainForm,
            function (response) {
              t.loading = false;
              t.dialogDomainFormVisible = false;
              t.sourceIpListVisible = false;
              t.sourceIpListValue = "";
              t.sourceIpList = [];
              t.onCurrentChange(t.currentPage);
            },
            function () {
              t.loading = false;
            }
          );
        }
      });
    },

    onClickCreateDomain() {
      var t = this;
      t.domainTitle = "新增网站";
      t.domainType = "new";
      t.dialogDomainFormVisible = true;
      t.getSSL();
    },

    handleEdit(data) {
      var t = this;
      data.loading = true;
      t.getSSL();
      JXAjax(
        "post",
        "/waf/waf_get_domain",
        { domain: data.domain },
        function (response) {
          data.loading = false;
          var oGetData = response.data.message;
          t.domainForm.domain = oGetData.domain;
          t.domainForm.ssl_domain = oGetData.ssl_domain;
          t.domainForm.proxy_pass_https = oGetData.proxy_pass_https;
          t.domainForm.source_http_port = oGetData.source_http_port;
          t.domainForm.advanced_conf = oGetData.advanced_conf;
          t.domainForm.force_https = oGetData.force_https;
          t.domainForm.pre_proxy = oGetData.pre_proxy;
          t.domainForm.real_ip_conf = oGetData.real_ip_conf;
          t.domainForm.balance_type = oGetData.balance_type;
          
          
          t.sourceIpList = oGetData.source_ip
            ? oGetData.source_ip.split(",")
            : [];
          t.domainForm.isVisiblePopover = false;

          if (oGetData.http == "true") {
            t.domainForm.checkListProtocol.push("HTTP");
          }
          if (oGetData.https == "true") {
            t.domainForm.checkListProtocol.push("HTTPS");
          }
          if (oGetData.ssl_source == "custom") {
            t.selectTabsValue = "1"
          } else { 
            t.selectTabsValue = "0"
          }
          t.domainTitle = '编辑网站';
          t.domainType = "edit";
          t.dialogDomainFormVisible = true;
        },
        function () {
          data.loading = false;
        },
        "no-message"
      );
    },
    handleProtection(data) {
      window.location.href = "/#/protection/" + data.domain;
    },

    handleDelete(data) {
      var t = this;
      t.loading = true;
      JXAjax(
        "post",
        "/waf/waf_del_domain",
        { domain: data.domain },
        function (response) {
          data.isVisiblePopover = false;
          t.loading = false;
          t.onCurrentChange(t.currentPage);
        },
        function () {
          t.loading = false;
        }
      );
    },

    getSSL() {
      var t = this;
      JXAjax(
        "get",
        "/waf/waf_get_ssl_manage_list",
        {},
        function (response) {
          t.sslOptions = response.data.message;
        },
        function () {

        }
      );
    },
    onCurrentChange() {
      if (this.dataSearch == "") {
        this.getData(this.currentPage)
      } else {
        this.onChangeSearch()
      }
    },
    onChangeSearch() {
      var t = this;
      if (t.dataSearch) {
        JXAjax(
          "post",
          "/waf/waf_get_domain_search_list",
          { page: t.currentPage, domain: t.dataSearch },
          function (response) {
            t.loadingPage = false;
            t.tableData = response.data.message;
            t.currentPage = response.data.now_page;
            t.tableTotal = response.data.count;
            t.tableData.forEach(item => {
              item.isVisiblePopover = false;
            })
          },
          function () {
            t.loadingPage = false;
          },
          "no-message"
        );
      } else {
        t.getData(1)
      }

    },
  },
};
</script>
<style>

.domain-setting {
  float: right;
}
.domain-redirect {
  margin-top: 5px;
  margin-bottom: 0px !important;
}

.el-form-item.is-required .redirect-box .el-form-item__label:before {
  content: "";
}

.no-padding {
  padding: 0;
}
.node-detail span {
  font-size: 12px;
}
.domain-tabs {
  margin-bottom: 18px;
}
.el-tabs-myitem {
  color: #409eff;
  height: 40px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  line-height: 40px;
  display: inline-block;
  list-style: none;
  font-size: 14px;
  font-weight: 500;
  position: relative;
}
.el-table__body p {
  font-size: 14px;
  line-height: 30px;
}
.el-table__body .el-tag {
  margin-right: 5px;
}
.col-item-box {
  display: flex;
}
.col-item-content {
  flex:1;
}
.col-item-protection-title {
  display: inline-block;
  width: 120px;
}
</style>
